<template>
  <div class="app">
    <el-container style="height: 100%; border: 1px solid #eee">
      <!-- 头部 -->
      <el-header style="text-align: right; font-size: 14px;height:50px;">
          <blogHeader></blogHeader>
      </el-header>
      <el-container>
        <!-- 左侧导航 -->
        <el-aside width="220px" style="background-color: #fff">
          <blogIndex></blogIndex>
        </el-aside>
        <!-- 主体body -->
        <el-main>
        </el-main>
      </el-container>
    </el-container>
  
   
    </div>
</template>

<script>
import blogIndex from '../components/home/BlogIndex.vue'
//import headerNav from '../components/home/headerNav.vue'
import blogHeader from '@/components/common/BlogHeader.vue'
export default {
  name: 'index',
  // blogHeader/blogFooter组件给申明到components里面然后在template里面使用
  components: { blogIndex ,blogHeader},

}
</script>



